@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');
* {
    margin: 0;
    padding: 0;
    font-family: 'Press Start 2P', monospace;
}

html,
body {
    overflow: hidden;
}

.webgl {
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
    z-index: -1;
}

h3,
h2 {
    color: white;
    font-size: 15px;
    margin: 0.8em;
    font-weight: 100;
}

.container {
    margin: 30px;
}